<template>
  <v-dialog v-model="openDialog" persistent :max-width="800" class="popup">
    <v-card class="popup__content">
      <v-card-title>
        <v-spacer></v-spacer>
        <h3 class="popup__title">Ошибка</h3>
        <v-spacer></v-spacer>
      </v-card-title>
      <v-card-text>
        <p class="popup__text">{{ text }}</p>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="openDialog = false" class="popup__button">Закрыть</v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "PopupError",
  props: ["dialog", "text"],
  components: {},
  data: () => {
    return {
      openDialog: false,
    };
  },
  watch: {
    dialog() {
      this.openDialog = this.dialog;
    },
    openDialog() {
      if (!this.openDialog) {
        this.$emit("update:dialog", this.openDialog);
      }
    },
  },
  methods: {},
};
</script>

<style lang="scss">
@import "../assets/scss/main.scss";

div .popup {
  &__content {
    padding: 40px;

    @include for-size(tablet) {
      padding: 20px;
    }

    .v-card__actions {
      .v-btn {
        padding: 20px 40px;
        // @include text-form();
        font-family: "Montserrat-Regular";
        font-weight: 400;
        font-size: 18px;
        color: $white;
        background: $black;
        border-radius: $border-radius;
        transition: all 0.5s;

        @include for-size(mobile) {
          padding: 20px 30px;
          font-size: 14px;
        }

        &:hover {
          font-family: "Montserrat-Medium";
          font-weight: 500;
          transform: scale(1.1);
        }
      }
    }
  }

  &__title {
    @include title(red);
    font-family: "Montserrat-Regular";
    text-align: center;
  }

  &__text {
    margin-top: 20px;
    @include text-form();
    font-family: "Montserrat-Regular";
    text-align: center;
    color: $black;
  }
}
</style>
